import { ICusPeoplePickerState } from "./ICusPeoplePickerState";
import { BaseComponent, assign } from "office-ui-fabric-react/lib/Utilities";
import React = require("react");
import { sp } from "@pnp/sp";
import "@pnp/polyfill-ie11";
import styles from "./CusPeoplePicker.module.scss";
import {
  IBasePickerSuggestionsProps,
  NormalPeoplePicker,
  ValidationState
} from "office-ui-fabric-react/lib/Pickers";
import { IPersonaProps } from "office-ui-fabric-react/lib/Persona";
const suggestionProps: IBasePickerSuggestionsProps = {
  suggestionsHeaderText: "建议结果",
  mostRecentlyUsedHeaderText: "匹配到的结果",
  noResultsFoundText: "没有找到",
  loadingText: "加载中",
  showRemoveButtons: true,
  suggestionsAvailableAlertText: "选择建议",
  suggestionsContainerAriaLabel: "匹配到的结果"
};

import * as objectAssign from "object-assign";
export default class CusPeoplePicker extends BaseComponent<
  any,
  ICusPeoplePickerState
> {
  private people: (IPersonaProps & { key: string | number })[] = [];

  constructor(props: any) {
    super(props);
    if (this.state === undefined) {
      this.state = {
        currentPicker: this.props.currentPicker,
        delayResults: false,
        peopleList: [],
        mostRecentlyUsed: [],
        currentSelectedItems: props.selectedItems || undefined
      };
    }
    // console.log(this.state);
  }
  public render(): React.ReactElement<any> {
    return (
      <div className={styles["ms-Picker-container"]}>
        {this.props.label && this.props.label.trim() !== "" ? (
          <label className={styles["ms-Label"]}>
            {this.props.label}
            {this.props.required === true ? (
              <span style={{ color: "red" }}>*</span>
            ) : (
              ""
            )}
          </label>
        ) : (
          ""
        )}
        <div
          aria-expanded="false"
          aria-live="assertive"
          aria-disabled="true"
          className={styles["ms-TextField-fieldGroup"]}
        >
          <NormalPeoplePicker
            onResolveSuggestions={this._onFilterChanged}
            onEmptyInputFocus={this._returnMostRecentlyUsed}
            getTextFromItem={this._getTextFromItem}
            pickerSuggestionsProps={suggestionProps}
            className={"ms-PeoplePicker"}
            key={"normal"}
            onRemoveSuggestion={this._onRemoveSuggestion}
            onValidateInput={this._validateInput}
            removeButtonAriaLabel={"Remove"}
            itemLimit={this.props.itemLimit}
            onItemSelected={this._onItemSelected}
            inputProps={{
              onBlur: (ev: React.FocusEvent<HTMLInputElement>) =>
                console.log("onBlur called"),
              onFocus: (ev: React.FocusEvent<HTMLInputElement>) =>
                console.log("onFocus called"),
              "aria-label": this.props.label
            }}
            onInputChange={this._onInputChange}
            selectedItems={this.state.currentSelectedItems}
            onChange={this.SelectedChanged}
            /*onRenderItem={CusSelectedItem as (props: IPickerItemProps<IPersonaProps>) => JSX.Element}*/
          />
        </div>
      </div>
    );
  }

  private _onFilterChanged = (
    filterText: string,
    currentPersonas: IPersonaProps[],
    limitResults?: number
  ): IPersonaProps[] | Promise<IPersonaProps[]> => {
    if (filterText) {
      return this._filterPersonasByText(filterText, limitResults).then(
        (): IPersonaProps[] => {
          return this.people;
        }
      );
    } else {
      return [];
    }
  };
  private _returnMostRecentlyUsed = (
    currentPersonas: IPersonaProps[]
  ): IPersonaProps[] | Promise<IPersonaProps[]> => {
    let { mostRecentlyUsed } = this.state;
    mostRecentlyUsed = this._removeDuplicates(
      mostRecentlyUsed,
      currentPersonas
    );
    return mostRecentlyUsed;
  };
  private _getTextFromItem(persona: IPersonaProps): string {
    return persona.text as string;
  }
  private SelectedChanged = (items?: IPersonaProps[]): void => {
    // const newps:IPersonaProps[]=items;
    // const processedItem =objectAssign({}, items);
    // console.log(items);
    this.setState({ currentSelectedItems: items });
    if (this.props.SelectedChanged) {
      this.props.SelectedChanged(items);
    }
  };
  private _onRemoveSuggestion = (item: IPersonaProps): void => {
    const {
      peopleList,
      currentSelectedItems,
      mostRecentlyUsed: mruState
    } = this.state;
    const indexPeopleList: number = peopleList.indexOf(item);
    const indexMostRecentlyUsed: number = mruState.indexOf(item);
    if (indexPeopleList >= 0) {
      const newPeople: IPersonaProps[] = peopleList
        .slice(0, indexPeopleList)
        .concat(peopleList.slice(indexPeopleList + 1));
      this.setState({ peopleList: newPeople });
    }
    if (indexMostRecentlyUsed >= 0) {
      const newSuggestedPeople: IPersonaProps[] = mruState
        .slice(0, indexMostRecentlyUsed)
        .concat(mruState.slice(indexMostRecentlyUsed + 1));
      this.setState({ mostRecentlyUsed: newSuggestedPeople });
    }
  };
  private _validateInput = (input: string): ValidationState => {
    if (input.indexOf("@") !== -1) {
      return ValidationState.valid;
    } else if (input.length > 1) {
      return ValidationState.warning;
    } else {
      return ValidationState.invalid;
    }
  };
  private _onInputChange(input: string): string {
    const outlookRegEx: RegExp = /<.*>/g;
    const emailAddress: RegExpExecArray = outlookRegEx.exec(input);
    if (emailAddress && emailAddress[0]) {
      return emailAddress[0].substring(1, emailAddress[0].length - 1);
    }
    return input;
  }
  private _removeDuplicates(
    personas: IPersonaProps[],
    possibleDupes: IPersonaProps[]
  ): IPersonaProps[] {
    return personas.filter(
      persona => !this._listContainsPersona(persona, possibleDupes)
    );
  }
  private _filterPersonasByText(
    filterText: string,
    limitResults?: number
  ): Promise<void> {
    this.people.length = 0;
    let topnum: number = limitResults ? limitResults : 5;
    //console.log(`filterText:${filterText}`);
    return sp.web.siteUsers
      .filter(
        `substringof('${filterText}',Title) or substringof('${filterText}',LoginName)`
      )
      .top(topnum)
      .get()
      .then(users => {
        users.map(user => {
          //  console.log(user);
          this.people.push({ key: user.Id, text: user.Title });
        });
      });
  }
  private _listContainsPersona(
    persona: IPersonaProps,
    personas: IPersonaProps[]
  ): boolean {
    if (!personas || !personas.length || personas.length === 0) {
      return false;
    }
    return personas.filter(item => item.text === persona.text).length > 0;
  }
  private _onItemSelected = (
    item: IPersonaProps & { key: string | number }
  ): Promise<IPersonaProps> => {
    const processedItem = objectAssign({}, item);
    processedItem.text = `${item.text}`;
    return new Promise<IPersonaProps>((resolve, reject) =>
      setTimeout(() => resolve(processedItem), 250)
    );
  };
  private _getpersonbyid(id: number): Promise<IPersonaProps[]> {
    return sp.web.siteUsers
      .getById(id)
      .get()
      .then(
        (user): IPersonaProps[] => {
          var per: IPersonaProps & { key: string | number } = {
            key: user.Id,
            text: user.Title
          };
          let arrpers: IPersonaProps[] = [];
          arrpers.push(per);
          return arrpers;
        }
      );
  }
  public componentWillReceiveProps(nextProps: any): void {
    // console.log(nextProps);
    if (this.props.SelectedChanged && nextProps.selectedItems) {
      this.setState({ currentSelectedItems: nextProps.selectedItems });
    }
  }
  public componentDidMount(): void {
    // tslint:disable-next-line:no-empty
    if (this.state.currentPicker && this.state.currentPicker > 0) {
    }
    // tslint:disable-next-line:one-line
    else {
      if (this.props.defaultMe) {
        sp.web.currentUser.get().then(user => {
          var per: IPersonaProps & { key: string | number } = {
            key: user.Id,
            text: user.Title
          };
          let arrpers: IPersonaProps[] = [];
          arrpers.push(per);
          /* arrpers.push({ key: 2,
        text: "test"} as  IPersonaProps & { key: string | number });*/
          this.setState({
            currentPicker: user.Id,
            currentSelectedItems: arrpers
          });
        });
      }
      //  console.log(this.props.selectedItems);
      // console.log(this.state);
    }
  }
}
